<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <v-container fluid>

  <v-row>
    <v-col cols="4">
      <h3>Filters</h3>
      <button v-if="!filters.length" disabled loading />

      <v-expansion-panels popout>
        <v-expansion-panel hover focusable popout
                           v-for="filter in filters"
                           :key="filter.name">
          <v-expansion-panel-header>
            {{ filter.name }}
          </v-expansion-panel-header>
          <v-expansion-panel-content v-if="filter.values.length < 20">
<!-- Found this accidentally: Multiple v-switch's can share the same v-model by using an array. Could be useful for unification with v-autocomplete model-wise -->
            <v-checkbox v-for="value in filter.values"
                        :key="value.label"
                        :label="computeFilterLabel(value)"
                        :disabled="!value.enabled"
                        v-model="value.selected"
                        @change="onFilterChange()" />
          </v-expansion-panel-content>

          <v-expansion-panel-content v-else>
            <span v-if="filter.name === 'Interlocutors'">
              <small style="opacity:0.66">Sorry that some names which should be the same aren't and some which shouldn't be are. Data quality is difficult.</small>
            </span>
            <v-autocomplete chips deletable-chips
                            clearable multiple solo
                            :items="computeListedItems(filter)"
                            v-model="filter.listed"
                            @change="onListedFilterChange(filter)" />
          </v-expansion-panel-content>
        </v-expansion-panel>

      </v-expansion-panels>
    </v-col>

    <v-col cols="8">
      <v-progress-circular v-if="!!restCallsGoing" indeterminate style="width: 100%; height: 28px" />
      <span v-else>
        <h3 v-if="!results.length">
          Found nothing.
          <span style="font-size:medium">Perhaps the filters are set in an impossible combination or you may need to
            <span v-if="loggedIn">logout and login as another user</span>
            <a v-else :href="loginRdrHref">login</a>
            .
          </span>
        </h3>
        <h3 v-else>{{ truncatedResults.length }} of {{ results.length }} shown</h3>
      </span>

      <v-checkbox label="Shuffle" v-model="shuffle" />
      <hr>

<!--      <p class="stats-para" v-if="false">-->
<!--        (Stats go here)-->
<!--      </p>-->

      <p v-for="result,i in truncatedResults"
         :key="i"
         v-html="result" />

      <v-btn v-if="results.length > resultsShown"
             @click="showMore()">Show more...</v-btn>

      <Notifications :alerts="alerts" :floating="true" />
    </v-col>

  </v-row>
  </v-container>
</template>

<script>
  import router from '../plugins/router.js';
  import shuffle from '../lib/shuffle.js';
  import AxiosHelper from '../components/AxiosHelper.vue';
  import HtmlEntry from '../components/HtmlEntry.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: {HtmlEntry, Notifications},
    mixins: [AxiosHelper],

    props: ['loggedIn'],

    data: () => ({
      results: [],
      shuffle: true,
      resultsShown: 30,

      loginRdrHref: '/',

      filtersFirstLoaded: true,
      filters: []
    }),

    computed: {
      intermediateResults () {
        if (this.shuffle) {
          let shuffled = Array.from(this.results);
          shuffle(shuffled);
          return shuffled;
        }

        return this.results;
      },

      truncatedResults () {
        return this.intermediateResults.filter((result, index) => index < this.resultsShown);
      }
    },

    methods: {
      computeFilterLabel(filterValue) {
        if (filterValue.visibleCount === filterValue.totalCount)
          return filterValue.label + ' (' + filterValue.visibleCount + ')';
        else
          return filterValue.label + ' (' + filterValue.visibleCount + '/' + filterValue.totalCount + ')';
      },

      computeListedItems(filter) {
        return filter.values
          .filter(v => v.visibleCount > 0 || v.selected)
          .sort((a,b) => (b.selected && 1000000 || 0) + b.visibleCount - (a.selected && 1000000 || 0) - a.visibleCount)
          .map(v => ({
            text: this.computeFilterLabel(v),
            value: v.label,
            disabled: !v.enabled
          }));
      },

      loadFilters() {
        let queryFromUrl = this.$route.query || {};

        function forceArray(elem) {
          return (Array.isArray(elem) && elem) || [elem];
        }

        let firstQuery = Object.keys(queryFromUrl)
          .flatMap(k => forceArray(queryFromUrl[k])
            .map(v => k + '=' + v))
          .join('&');

        this.credentialedAxios('loading filters', axios => axios.get(this.$apiUrl + '/diary/gagmine/filters?' + firstQuery),
          this.setFilterData);
      },

      loadFiltersFromExistingQuery() {
        this.credentialedAxios('loading filters', axios => axios.get(this.$apiUrl + '/diary/gagmine/filters?' + this.query()),
          data => {this.setFilterData(data);
            this.updateUrlFromSelections()});
      },

      setFilterData(data) {
        let filtersDict = {};
        this.filters.forEach(f => filtersDict[f.name] = f.values.filter(v => v.selected).map(v => v.label));

        data.forEach(f => {
          f.listed = filtersDict[f.name] || [];
          f.values.forEach(v => v.selected = f.listed.indexOf(v.label) >= 0);
        });
        this.filters = data;

        if (this.filtersFirstLoaded) {
          this.filtersFirstLoaded = false;
          this.updateSelectionsFromUrl();
          this.loadData();
        }
      },

      query () {
        return this.filters
          .flatMap(f => f.values.filter(v => v.selected).map(v => f.name + '=' + v.label))
          .join('&');
      },

      updateSelectionsFromUrl() {
        let queryFromUrl = this.$route.query || {};

        function containsOrEquals(elem, value) {
          return (Array.isArray(elem) && elem.indexOf(value) >= 0)
              || elem === value;
        }

        let filters = this.filters;
        Object.keys(queryFromUrl)
          .forEach(k => filters
            .filter(f => f.name === k)
            .forEach(f => {
              f.values.forEach(v => v.selected = containsOrEquals(queryFromUrl[k], v.label));
              f.listed = f.values.filter(v => v.enabled && v.selected).map(v => v.label);
            }));
        this.filters = filters;
      },

      loadData() {
        this.resultsShown = 30;
        this.credentialedAxios('loading data', axios => axios.get(this.$apiUrl + '/diary/gagmine/query?' + this.query()),
          data => this.results = data);
      },

      onListedFilterChange(filter) {
        filter.values.forEach(v => v.selected = filter.listed.indexOf(v.label) >= 0);
        this.onFilterChange();
      },

      onFilterChange() {
        this.loadData();
        this.loadFiltersFromExistingQuery();
      },

      updateUrlFromSelections() {
        let query = {};
        this.filters.forEach(f => query[f.name] = f.values
          .filter(v => v.selected)
          .map(v => v.label));
        router.replace({query});

        this.updateLoginRdrHrefFromUrl();
      },

      updateLoginRdrHrefFromUrl() {
        // Important example: https://teonistor.herokuapp.com/login-rdr?target=https%3A%2F%2Fteonistor.herokuapp.com%2F%23%2Fgoldmine%3FYear%3D2018%26Year%3D2019%26Year%3D2021%26Category%3DWork
        this.loginRdrHref = '/login-rdr?target=' + encodeURIComponent(location.href);
      },

      showMore() {
        this.resultsShown += 50;
      }
    },

    mounted() {
      this.loadFilters();
      this.updateLoginRdrHrefFromUrl();
    }
  }
</script>

<style>
  .col-4 {
    height: 98vh;
    overflow: auto;
    position: sticky;
    top: 0;
  }
  .stats-para {
    border-bottom: solid darkgray 1px;
    padding-bottom: 8px;
  }
</style>
